body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#public-header,
#public-footer {
  padding: 0 20px;
}

#public-main {
  clear: both;
  padding: 0 20px;
}

#logo {
  float: left;
}

#to-admin {
  float: right;
}

#to-admin a {
  display: inline-block;
  padding: 0 5px;
  text-transform: uppercase;
}

.ant-layout-header {
    padding: 0;
    background-color: #1890ff;
}

.ant-layout-header a {
    color: white;
}

.ant-tabs-nav .ant-tabs-tab {
    text-transform: capitalize;
}

.ant-layout {
    background-color: white;
}

.ant-input-number {
    width: 100%;
}

.float-right {
    float: right;
}

.float-left {
    float: left;
}

.delete-red {
    color:#f5222d
}

h1 {
    /* float: left; */
}
.link {
    cursor: pointer;
}

.m-button,
.link {
    margin: 10px 0;
    float: right;
}

/* links on right side of index page */
.right-link {
    display: block;
    margin-top: 10px;
}

/* flex */
.flex {
    display: flex;
}

@media only screen and (max-width: 600px) {
    h1 {
        float: none;
    }
    .m-button {
        margin-top: 0;
        float: none;
        width: 100%;
    }
}

.drawer {
    position: fixed;
    overflow: auto;
    top: 0;
    right: 0;
    bottom: 0;
    width: 60%;
    background-color: white;
    padding: 10px;
    z-index: 1001;
    border-left: 1px solid #eee;
    margin-right: -60%; /* Safari */
    transition: margin-right 0.3s;
}
.drawer.small {
    width: 30%;
    margin-right: -30%;
}
.drawer.show, 
.drawer-overlay.show {
    margin-right: 0;
}

.drawer-overlay  {
    position: fixed;
    overflow: hidden;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    background-color: transparent;
    z-index: -1; /* Safari */
    transition: all 0.3s;
}

.drawer-overlay.show {
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1000;
}
@media only screen and (max-width: 600px) {
    .drawer {
        width: 100%;
        margin-right: -100%;
    }
}


